<template>
  <div class="hello">
    <h1>{{ msg }}</h1>
    <div>
      <label>Name</label>
      <input type="text" v-model="name">
      <b-button pill variant="success" @click="submitName()">Add</b-button>
    </div>
    <div>
       <ul>
        <li v-for="(personName,index) of names" v-bind:key="index">
          <div v-if="!personName.edit"> 
             <p>{{index}} : {{personName.name}}</p>
             <b-button pill variant="danger" @click="removeName(personName.key)">Remove</b-button>
             <b-button pill variant="warning" @click="setEditName(personName.key)">Edit</b-button>
          </div>
          <div v-else>
              <input type="text" v-model="personName.name">
              <b-button pill variant="success" @click="saveEdit(personName)" >Save</b-button>
              <b-button pill variant="dark" @click="cancelEdit(personName.key)">Cancel</b-button>
          </div>
        </li>
      </ul> 
    </div>
  </div>
</template>

<script>
import {namesRef} from '../firebase';

export default {
  name: 'HelloWorld',
  props: {
    msg: String
  },

  data(){
    return {
      name:'',
      names:[]
    }
    
  },

  methods: {
    submitName(){
      namesRef.push({name:this.name,edit:false});
      this.name="";
    },
    removeName(key){
      namesRef.child(key).remove();
    },
    setEditName(key){
      namesRef.child(key).update({edit:true});
    },
    cancelEdit(key){
      namesRef.child(key).update({edit:false})
    },
    saveEdit(person){
      const key=person.key;
      namesRef.child(key).set({name:person.name,edit:false})
    },
  },
  
  mounted (){
    this.mimba;
  },

  computed: {
    mimba: function () {
      return namesRef.on('value',snapshot =>{ 
        snapshot.forEach(res => {
          const data={
             'key':res.key,
             'name':res.val().name,
             'edit':res.val().edit
          }
          this.names.push(data);
          });

        } );
    }
  }
}
</script>

